import inputVerification from './verification.js';
 export default {
    props: {
		//自定义传入的 class ，自定义样式
        set: {
            type: String,
            default: ''
        },
		//input 类型,默认 text
		type: {
		    type: String,
		    default: ''
		},
		// input字数限制
		maxLength: {
		    type: String,
		    default: '100'
		},
		// 组件对于上方的间距
		maTop: {
		    type: String,
		    default: '0'
		},
		// 组件对于下方的间距
		maBtm: {
		    type: String,
		    default: '0'
		},
		//输入框默认值
		inputValue: {
			type: String,
		    default:''
		},
		//是否禁用
		disabled:{
			default:false
		},
		// input placeholder 提示文字
		placeholder: {
		    type: String,
		    default: '请输入内容'
		},
		// input 校验
		verification: {
		    type: Array,
		    default: null
		},
		// input 校验提示语
		verificationTip: {
		    type: Array,
		    default: null
		},
		// input左边的文字
		leftText: {
		    type: String,
		    default: ''
		},
		// input左边自定义的样式
		leftClass: {
		    type: String,
		    default: ''
		},
		// input右边的文字
		rightText: {
		    type: String,
		    default: ''
		},
		// input右边自定义的样式
		rightClass: {
		    type: String,
		    default: ''
		},
		//是否显示清除按钮,默认显示
		clearShow:{
			type: Boolean,
			default: true
		}
    },
    data() {
        return {
			value: '',
			marginTop: parseFloat(this.maTop) / 375 * 750 + 'rpx',
			marginBottom: parseFloat(this.maBtm) / 375 * 750 + 'rpx',
			isShowEye:false,//是否显示密码
		};
    },
	mounted() {
		this.value = this.inputValue
	},
	methods: {
		  /**
		 * @desc 密码显隐操作
		 */
		passwordClick() {
			this.isShowEye = !this.isShowEye;
		},
		/**
		 * @desc 左边点击事件
		 */
		leftClick(){
			//传值到父组件和父组件双向绑定
			this.$emit('leftClick', this.value);
		},
		/**
		 * @desc 右边点击事件
		 */
		rightClick(){
			//传值到父组件和父组件双向绑定
			this.$emit('rightClick', this.value);
		},
		 /**
		 * @desc 监听输入框的值
		 */
		keyInput(event) {
			let value = event.detail.value;
			this.value = value;
			//传值到父组件和父组件双向绑定
			this.$emit('input', this.value);
		},
		/**
		 * @desc 清楚输入框的值
		 */
		clear(flag) {
		    this.value = '';
		    // 双向绑定到父组件（双向绑定，不能弹出提示）
		    this.$emit('input', this.value);
		},
		/**
		 * @param 获取带校验的输入框的值
		 */
		 /**
		 * @name:获取输入框的值
		 * @return value || undefined
		 */
		getValue (flag) {
			if (!flag) {
				// 判断是否有校验
				if (this.verification && this.verification.length > 0) {
					for (let i = 0; i < this.verification.length; i++) {
						// 获取校验的名称
						const verificationName = this.verification[i]
						// 判断校验是否正确
						const verificationResult = inputVerification.verificationFuc({name: verificationName, value: this.value})
						if (!verificationResult.flag) {
							// 弹出错误信息
							// 判断有没有自定义的弹出信息
							if (this.verificationTip && this.verificationTip[i]) {
								uni.showToast({
									icon: 'none',
									title: this.verificationTip[i] || '',

									duration: 2000,

									// mask: true
								});
							} else {
								// 弹出方法自带的提示语
								uni.showToast({
									icon: 'none',
									title: verificationResult.tip || '',

									duration: 2000,

									// mask: true
								});
							}
							return '';
						}
					}
				}
			}
			return this.value || '';
		}
	}
}
